export const jwtHandleError = (error) => {
  if (error.response) {
    switch (error.response.status) {
      case 400:
        alert("  .");
        break;

      case 401:
        alert("    .");
        window.location.href = "/signin";
        break;

      case 403:
        alert(
          "     .\n   ."
        );
        break;

      case 404:
        window.location.href = "/notfound";
        break;

      case 500:
        alert("  .\n   .");
        break;

      case 503:
        alert("  .\n   .");
        break;
      default:
        alert(
          `    .\n    .\n( : ${error.response.status})`
        );
    }
  } else if (error.request) {
    alert("  .\n  .");
  }
};
